        margin-bottom: 8px !important;
}
div.EditarArquivo div.cont table tr td label {
          vertical-align: baseline;
}
div.EditarArquivo div.cont table tr td:first-child:not(:last-child) {
          padding-right: 8px;
          text-align: right;
}
div.EditarArquivo div.cont table tr td:nth-child(2) {
          display: inline-flex;
          justify-content: space-between;
          width: 100%;
}
div.EditarArquivo div.cont table tr td:nth-child(2) input[type=text], div.EditarArquivo div.cont table tr td:nth-child(2) textarea {
            width: 520px;
            padding: 4px 6px;
}
div.EditarArquivo div.cont table tr td:nth-child(2) > span {
            margin-left: 8px;
}
div.EditarArquivo div.cont table tr td:nth-child(2) > span.charCounter {
              width: 50px;
              display: inline-flex;
              align-items: flex-end;
              justify-content: flex-end;
              color: #898989;
}
div.EditarArquivo div.cont table tr td div.addUrl {
          display: inline;
          float: right;
          background-color: transparent;
          color: #DDD;
          height: 32px;
          line-height: 32px;
          border: 1px solid #DDD;
          border-radius: 16px;
          padding: 0 12px;
          transition: all .2s;
          cursor: pointer;
}
div.EditarArquivo div.cont table tr td div.addUrl:hover {
            background: #0073aa;
            border-color: #0073aa;
            color: #FFF;
}
div.EditarArquivo div.cont table tr:last-child td {
        margin: 8px 0 0 0;
}
div.EditarArquivo div.cont div.actions {
      padding: 0 24px 24px 24px;
      display: flex;
      justify-content: space-between;
}
div.EditarArquivo div.cont div.actions button {
        border-width: 0;
        color: #FFF;
        padding: 16px 24px;
        border-radius: 2px;
        box-shadow: inset 0 -2px 2px rgba(0, 0, 0, 0.24);
        cursor: pointer;
}
div.EditarArquivo div.cont div.actions button.cancelar {
          background-color: #fe4c4c;
}
div.EditarArquivo div.cont div.actions button.adicionar {
          background-color: #219653;
}

div.AdicionarEtapa {
  position: absolute;
  top: 0;
  left: -20px;
  width: calc(100% + 20px);
  height: calc(100vh - 32px);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: height ease-out .4s;
}
div.AdicionarEtapa.fechado {
    height: 0;
}
div.AdicionarEtapa.fechado > div.cont {
      transform: translateY(-80vh);
}
div.AdicionarEtapa div.cont {
    width: 720px;
    background-color: #FFF;
    border-radius: 4px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.48);
    z-index: 2;
    transform: translateY(0);
    animation: addEtp ease-in .4s;
    transition: transform ease-out .4s;
}
@keyframes addEtp {
from {
    transform: translateY(-80vh);
}
to {
    transform: translateY(0);
}
}
div.AdicionarEtapa div.cont div.title {
      background-color: #0073aa;
      position: relative;
      padding: 0 12px;
      border-radius: 2px 2px 0 0;
}
div.AdicionarEtapa div.cont div.title h3 {
        line-height: 40px;
        margin: 0;
        color: #FFF;
}
div.AdicionarEtapa div.cont table {
      padding: 24px;
      border-spacing: 0;
      width: 100%;
}
div.AdicionarEtapa div.cont table tr td:first-child {
        max-width: min-content;
        max-width: -moz-min-content;
        padding-right: 8px;
}
div.AdicionarEtapa div.cont table tr td:last-child {
        width: 100%;
}
div.AdicionarEtapa div.cont table tr td:last-child input {
          width: 100%;
}
div.AdicionarEtapa div.cont div.actions {
      padding: 0 24px 24px 24px;
      display: flex;
      justify-content: space-between;
}
div.AdicionarEtapa div.cont div.actions button {
        border-width: 0;
        color: #FFF;
        padding: 16px 24px;
        border-radius: 2px;
        box-shadow: inset 0 -2px 2px rgba(0, 0, 0, 0.24);
        cursor: pointer;
}
div.AdicionarEtapa div.cont div.actions button.cancelar {
          background-color: #fe4c4c;
}
div.AdicionarEtapa div.cont div.actions button.adicionar {
          background-color: #219653;
}

div.Modal {
  box-sizing: border-box;
  position: absolute;
  top: 20px;
  left: -20px;
  width: 100%;
  height: calc(100vh - 32px);
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  padding: 40px 20px;
  overflow: hidden;
}
div.Modal.fechado {
    display: none;
}
div.Modal div.modal-cont {
    display: flex;
    flex-flow: row nowrap;
    align-items: stretch;
    background-color: #FFF;
    border-radius: 2px;
    border-top-width: 2px;
    border-top-style: solid;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.12);
    z-index: 2;
    transition: transform ease-out .4s, background-color ease-in .1s;
    animation: modal ease-in .8s;
}
@keyframes modal {
0% {
    transform: translateY(200px);
    background-color: #FFF;
}
49% {
    transform: translateY(0);
    background-color: #FFF;
}
50% {
    transform: translateY(0);
    background-color: #898989;
}
80% {
    transform: translateY(0);
    background-color: #FFF;
}
}
div.Modal div.modal-cont div.modal-txt {
      padding: 12px 16px;
}
div.Modal div.modal-cont div.modal-txt h4 {
        margin: 0;
        padding: 0;
        font-size: x-small;
        text-transform: uppercase;
        font-weight: bold;
}
div.Modal div.modal-cont div.modal-ok button {
      width: 40px;
      height: 100%;
      border: 0;
      background-color: rgba(0, 0, 0, 0.04);
}
div.Modal div.modal-cont div.modal-ok button:active {
        background-color: rgba(0, 0, 0, 0.2);
}
div.Modal.sucesso > div.modal-cont {
    border-top-color: #219653;
}
div.Modal.sucesso > div.modal-cont h4 {
      color: #219653;
}
div.Modal.erro > div.modal-cont {
    border-top-color: #FE4C4C;
}
div.Modal.erro > div.modal-cont h4 {
      color: #FE4C4C;
}
div.Modal.atencao > div.modal-cont {
    border-top-color: #FFCC00;
}
div.Modal.atencao > div.modal-cont h4 {
      color: #FFCC00;
}
div.Modal.componente {
    align-items: center;
}
div.Modal.componente div.modal-cont {
      display: none;
}
div.Modal:active > div.modal-cont {
    background-color: #898989;
}




















